img {
  transform: none !important;
  width: 100%;
}

.banner-background-image {
  padding: 0 !important;
  margin: 0;
  min-height: 100vh;
  background: url(/static/media/banner_slider_4.618d8fe2.jpg);
  background-size: cover;
  /* animation: animateBg 276s linear infinite; */
  box-shadow: inset 0 0 0 2000px rgba(6, 6, 158, 0.288);
}

.pulse-earth-image {
  position: absolute;
  top: 8%;
  right: -10%;
  transform: translate(-50%, -50%);
  background: url(/static/media/earth.64546561.png);
  background-size: cover;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  box-shadow: inset 0 0 40px #120e2e;
  /* animation: animateEarth 36s linear infinite; */
  z-index: 10;
}
.pulse-background {
  position: absolute;
  top: 8%;
  right: -10%;
  transform: translate(-50%, -50%);
  background: rgb(3 8 63);
  background-size: cover;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  z-index: 5;
  box-shadow: 4px 10px 90px -2px #212124;
}
@keyframes animateEarth {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 719px 0;
  }
}
@keyframes animateBg {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 3952px 0;
  }
}

@media (max-width: 600px) {
  .pulse-background {
    top: 5%;
  }
  .pulse-earth-image {
    top: 5%;
  }
}

.searchbar-container-home {
  position: absolute;
  top: 15rem;
  left: 10rem;
  z-index: 15;
  max-width: 100%;
}
.search-input-bar .form-row {
  display: flex;
  flex-direction: row;
}
.searchbar-container-home .header-title-1 {
  font-weight: bold;
  color: lightgray;
}
.searchbar-container-home .header-title-2 {
  font-weight: bold;
  color: white;
  font-size: 300%;
}

.search-options {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.search-options .option {
  padding: 20px 20px 3px 0px;
  font-weight: bolder;
  font-size: 130%;
  color: white;
  padding-right: 50px;
  cursor: pointer;
  border-bottom: 4px solid white;
  border-radius: 0.25rem;
}
.search-options .option i {
  color: white;
  font-size: 150%;
  font-weight: bold;
  margin-right: 5px;
  margin-left: 25px;
}

.logo-input-container {
  display: flex;
  flex-direction: row;
}
.logo-input-container span {
  padding: 0.375rem 0.75rem;
  background-color: white;
  color: purple;
  font-weight: bold;
  padding-top: 10px;
  border-left: 2px solid gray;
  cursor: pointer;
}

.logo-input-container input {
  border-radius: 0px;
  border: 0px;
  margin-top: 5px;
}

.logo-input-container {
  background-color: white;
  padding: 5px;
}
.arrow-container {
  background-color: white;
  padding: 5px;
}

.logo-input-container.search-button {
  border: 5px solid white;
}
.logo-input-container.search-button span {
  border-radius: 0.5rem;
}

